.home{
    padding: 1rem;
}

.borderOnly{
    border: 4px solid white;
    padding: 1rem;
    height: 100%;
    width: 100%;
    border-radius: 30px;
}

.cellContainer{
    position: relative;
    background: white;
    padding: 1rem;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    height: 100%;
    width: 100%;
    border-radius: 30px;
}
.cellContainer > div{
    display: inherit;
    justify-content: inherit;
}
.cellContainer .cell{
    border-radius: 15px;
}